import SearchInput from "../../../components/SearchInput/SearchInput";

export default {
	data() {
		return {
			goodsList:[]
		}
	},
	components: {
	  SearchInput
	},
	onLoad: function (options) {
		uni.request({
			url:'http://localhost:8080/commodity/selectCom',
			method:'POST',
			data:{},
			success: res => {
				this.goodsList=res.data.message;
				console.log("士大夫"+res.data.message)
			}
		});
	  },
	  methods:{
		 change:function(e){
		 			 console.log(e);
		 			uni.showToast({
		 				title:"编号"+e,
		 				icon:"none"
		 			});
		 			uni.request({
		 				url: 'http://localhost:8080/commodity/selectCom?ccategoryid='+e,
		 				method: 'POST',
		 				data: {},
		 				success: res => {
		 					this.goodsList=res.data.message;
		 					console.log(res.data.message)
		 				},
		 				fail: () => {},
		 				complete: () => {}
		 			});
				}
	  } 
}
